/* the css file for my Personal Portfolio page */

body {
	font-family: 'Open Sans', sans-serif;
}

h1 {
	font-size: 38px;
	font-weight: bold;
	color: #28333a;
	letter-spacing: 1.4px;
	text-transform: uppercase;
}

h2 {
	font-size: 20px;
	color: gray;
	letter-spacing: 1px;
	text-transform: uppercase;
}

nav {
	min-height: 60px;
	position: fixed;
	width: 100%;
	top: 0px;
	background-color: #24292e;
	z-index: 1;
}

p {
	font-size: 16px;
	line-height: 1.5;
	letter-spacing: .25px;
}

table {
	width: 100%;
}

#about {
	color: white;
	background: linear-gradient(to top left,rgba(213, 58, 58, 0.7),rgba(58, 77, 213, 0.7)),
	url(/static/media/bg.59f69120.jpg) center center no-repeat;
	background-size: cover;
	text-shadow: 0 2px 2px rgba(0, 0, 0, .7);
	background-attachment: fixed;
	padding-bottom: 100px;
}

#about a {
	text-decoration: none!important;
	color: #fcf8e3;
}

#about h1 {
	color: #fff;
	font-size: 60px;
	text-align: center;
	margin-bottom: 40px;
	margin-top: 25px;
}

#about p {
	text-align: justify;
	color: #f9fbff;
}

#aboutContainer {
	padding-top: 50px;
}

#buttons {
	line-height: 53px;
	vertical-align: center;
	padding: 0px;
	list-style: none;
	float: right;
	margin-bottom: 0px;
}

#buttons li {
	display: inline;
}

#contact {
	background-color: #1d1d1d;
}

#contactContainer {
	text-align: center;
}

#contactContainer a {
	font-size: 20px;
}

#contactContainer div {
	margin-bottom: 10px;
}

#name {
	line-height: 53px;
	vertical-align: center;
	color: white;
	font-size: 18px;
	text-transform: uppercase;
	font-weight: bold;
	letter-spacing: 1.4px;
}

#projects {
	padding-bottom: 0px!important;
	padding-top: 0px!important;
	margin-top: 260px;
}

#projects h2 {
	margin-top: 30px;
	margin-bottom: 20px;
	color: #34434B;
}

#projects p {
	text-align: justify;
	color: #586368;
}

#projects .container {
	padding-top: 35px;
	padding-bottom: 35px;
}

#projects .row:nth-child(even) {
	background-color: #f5f5f5;
}

#skills {
	position: absolute;
	top: 320px;
	left: 0;
	right: 0;
}

#skills h1 {
	text-align: center;
}

#skillsContainer {
	background: #f5f5f5;
	border-radius: 10px;
	box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.18);
}

#skillsContainer h1 {
	margin-top: 0;
	margin-bottom: 0;
	color: #28333a;
}

#skillsContainer hr {
	border-top: 1px solid #d4d4d4;
	width: 100%;
	margin-bottom: -5px;
}

#skillsContent {
	text-align: left;
	padding: 20px;
	display: inline-block;
	width: 100%;
}

#skillsContent div {
	padding-bottom: 15px;
	padding-left: 7%;
	font-size: 16px;
}

#skillsContent span {
	color: #34434B;
	letter-spacing: .5px;
	font-weight: bold;
	vertical-align: middle;
}

#skillsContent img {
	max-height: 22px;
	padding-right: 10px;
}

#skillsHeader {
	background: #f5f5f5;
	width: 100%;
	border-radius: 10px 10px 0 0;
	padding: 18px;
}

.borderGrad {
	height: 5px;
	background-color: #337ab7;
	color: white;
	background: hsl(208, 56%, 46%); /* For browsers that do not support gradients */ /* For Safari 5.1 to 6.0 */ /* For Opera 11.1 to 12.0 */ /* For Firefox 3.6 to 15 */
	background: linear-gradient(to left,#3acfd5,#3a4ed5); /* Standard syntax */
}

.borderGradReverse {
	height: 5px;
	background-color: #337ab7;
	color: white;
	background: hsl(208, 56%, 46%); /* For browsers that do not support gradients */ /* For Safari 5.1 to 6.0 */ /* For Opera 11.1 to 12.0 */ /* For Firefox 3.6 to 15 */
	background: linear-gradient(to right,#3acfd5,#3a4ed5); /* Standard syntax */
}

.contact {
	text-decoration: none!important;
	color: white!important;
	text-shadow: 0 1px 1px rgba(0, 0, 0, 1);
}

.desc h1 {
	margin-top: 0px;
}

.fa {
	margin-right: 5px;
}

.imgDiv {
	text-align: center;
}

.imgDiv img {
	max-width: 100%;
	max-height: 500px;
	border-radius: 10px;
	box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.18);
}

.navlink {
	text-decoration: none;
	font-weight: bold;
	margin-left: 20px;
}

.navlink:hover, .navlink:visited, .navlink:link, .navlink:active {
	text-decoration: none;
}

.redirect {
	padding-top: 35px;
	padding-bottom: 35px;
}

.row {
	margin-left: 0px;
	margin-right: 0px;
}

.row:nth-child(even) .tags li {
	background: #973337;
}

.tags {
	list-style: none;
	padding: 0px;
}

.tags li {
	display: inline-block;
	outline: none;
	text-align: center;
	text-decoration: none;
	font: 14px/100% Arial, Helvetica, sans-serif;
	padding: .5em 2em .55em;
	box-shadow: none;
	color: #fef4e9;
	border: 0;
	background: #303f9f;
	margin-bottom: 3px;
	margin-right: 3px;
}

.vcenter {
  display: inline-block;
	vertical-align: middle;
	float: none;
}

@media only screen and (max-width: 992px) {
	h1 {
		font-size: 40px!important;
	}

	#about {
		padding-bottom: 35px;
	}

	#name {
		font-size: 14px!important;
	}

	#projects {
		margin-top: 0px;
	}

  #skills {
		position: static;
		margin-top: 35px;
		margin-bottom: 35px;
	}

	#skillsContent img {
		padding-right: 4px;
	}

	#skillsContent div {
		font-size: 14px;
	}

	.navlink {
		margin-left: 10px;
	}
}

@media only screen and (max-width: 400px) {
	h1 {
		font-size: 24px!important;
	}

	#skillsContent div {
		padding-left: 0px;
	}

	.navlink {
		margin-left: 5px;
		font-size: 12px
	}
}

